import { Dialog, Choice, BackgroundDialog, TextEffect, FinishedDialog } from "./Dialog";
import { Scene } from "./Scene";
import { Player } from "./Player";
import brokenGlass from "./Broken-Crack-Glass-PNG-Transparent-Image.png";
import brokenGlassSound from "./src/sound/00263.mp3";
import laughing from "./src/sound/laughing.mp3";
import crying from "./src/anna/crying.gif";
import screamer from "./src/sound/screamer.mp3";

import horrorMusic from "./src/sound/horror.mp3";
import sadHorror from "./src/sound/horrorSad.mp3";

import cold_short from "./src/sound/Cold/cold_short.mp3";
import cold_long from "./src/sound/Cold/cold_long.mp3";
import { Howl } from "howler";


export const dialogs = (scene, anna, messageInfo) => {
    if(localStorage.getItem("finished")){
        return ([
            new Dialog({
                messageText: "  "
            }),
            new Dialog({
                messageText: "    "
            }),
            new Dialog({
                messageText: " ",
                functionAfterMessage: () => {
                    window.scrollTo({
                        top: 0,
                        behavior: "smooth"
                    });
                    document.querySelector(".novel").remove();
                }
            })
        ])
    }
    return (
        [
            new Dialog({
                messageText: ",      .     ,       ",
                autor: "",
            }),
            new Dialog({
                messageText: "     !       -,          !",
                autor: "",
                choice: new Choice({
                    variants: [
                        {
                            variantText: "",
                            variantDialogs: [
                                new Dialog({
                                    messageText: ",   ",
                                    autor: "",
                                    runMusic: true,
                                    callbackOutside: (props) => {
                                        if(scene.musicPlayed)
                                            props.messageText = "    .     ,   , "
                                    }
                                })
                            ]
                        },
                        {
                            variantText: " ",
                            autor: "",
                            variantDialogs: [
                                new Dialog({
                                    messageText: "  ",
                                    callbackOutside: (props) => {
                                        if(scene.musicPlayed)
                                            props.messageText = ",    ,    .     "
                                    }
                                })
                            ]
                        }
                    ],
                })
            }),
            new Dialog({
                messageText: "  .  ,        .     ,      ",
                autor: ""
            }),
            new Dialog({
                messageText: "     .  ,       -.      , ,  .         ",
                autor: ""
            }),
            new Dialog({
                messageText: "   ?   ,   ",
                params: "nameModalWindow"
            }),
            new Dialog({
                messageText: "",
                callbackOutside: props => {
                    props.messageText = Player.name.toLowerCase().includes("") ? `${Player.name}?    ,    ` : `${Player.name}?     `
                }
            }),
            new Dialog({
                messageText: Dialog.genderMessage(Player.gender, {
                    maleMessage: "    ?",
                    femaleMessage: "    ?"
                }),
                choice: new Choice({
                    variants: [
                        {
                            variantText: " ",
                            variantDialogs: [
                                new Dialog({
                                    messageText: "      ,       ?   ",
                                    callbackOutside: () => scene.currentMusic.pause(),
                                }),
                            ],
                        },
                        {
                            variantText: " ",
                            variantDialogs: [
                                new Dialog({
                                    messageText: " , -    ,  -   ,   ", 
                                    callbackOutside: () => scene.currentMusic.pause(),
                                })
                            ]
                        }
                    ]
                })
            }),
            new Dialog({
                messageText: "      ,      .       ,   ",
                callbackOutside: () => {
                    scene.changeMusic(cold_short);
                }
            }),
            new Dialog({
                messageText: Dialog.genderMessage(Player.gender, {

                    maleMessage: "  -  ?    ?",
                    femaleMessage: "  -  ?    ?"
                }),
                choice: new Choice({
                    variants: [
                        {
                            variantText: "",
                            variantDialogs: [
                                new Dialog({
                                    messageText: " ?      ?     !"
                                })
                            ]
                        },
                        {
                            variantText: "",
                            variantDialogs: [
                                new Dialog({
                                    messageText: ", -  !      ,       !"
                                })
                            ]
                        },
                        {
                            variantText: " ",
                            variantDialogs: [
                                new Dialog({
                                    messageText: "      -   ,     !    !"
                                })
                            ]
                        }
                    ]
                })
            }),
            new Dialog({
                messageText: Dialog.genderMessage(Player.gender, {
                    maleMessage: `  ,       ,    ${Player.name}!    !`,
                    femaleMessage: `  ,       ,    ${Player.name}!    !`,
                })
            }),
            new Dialog({
                messageText: Dialog.genderMessage(Player.gender, {
                    maleMessage: `.          .      !`,
                    femaleMessage: `.          .      !`
                })
            }),
            new Dialog({
                messageText: "      ,    .     !"
            }),
            new Dialog({
                callbackOutside: () => {
                    scene.changeMusic(cold_long);
                },
                messageText: "       ,     ,    , " + Player.name + "?",
            }),
            new Dialog({
                messageText: "       ?",
                choice: new Choice({
                    variants: [
                        {
                            variantText: "  ",
                            variantDialogs: [
                                new Dialog({
                                    messageText: "   ?      .    ,     .     ",
                                })
                            ]
                        },
                        {
                            variantText: "",
                            variantDialogs: [
                                new Dialog({
                                    messageText: "    .     ?   , ,     .   ,   ?"
                                })
                            ]
                        }
                    ]
                })
            }),
            new Dialog({
                messageText: "  ,   ",
                animationOff: true,
                allAnimationStop: true,
                emotion: anna.emotions.glitched,
                timeout: 100,
                callbackOutside: () => {
                    Scene.voice = Scene.voices.angryVoice;
                    scene.currentMusic.pause();
                }
            }),
            new Dialog({
                messageText: "    ",
                animationOff: true,
                timeout: 150,
                allAnimationStop: true,
                callbackOutside: () => {
                    Scene.background = Scene.backgrounds.annaCloseBackground;
                    new Howl({
                        src: screamer
                    }).play();
                    scene.changeMusic(horrorMusic);
                },
                emotion: "none"
            }),
            new Dialog({
                messageText: "   ,    ",
                animationOff: true,
                timeout: 50,
                allAnimationStop: true,
                emotion: "none"
            }),
            new Dialog({
                messageText: "    .      ",
                animationOff: true,
                timeout: 100,
                allAnimationStop: true,
                emotion: "none"
            }),
            new Dialog({
                messageText: "  ,     ",
                timeout: 100,
                allAnimationStop: true,
                emotion: "none",
            }),
            new Dialog({
                messageText: "   ",
                callbackOutside: () => {
                    Scene.secondBackground = brokenGlass;
                    new Howl({
                        src: brokenGlassSound
                    }).play();
                },
                timeout: 50,
                allAnimationStop: true,
                emotion: "none",
                nextMessage: true,
            }),
            new Dialog({
                messageText: "  ",
                timeout: 50,
                allAnimationStop: true,
                emotion: "none",
                nextMessage: true,
            }),
            new Dialog({
                messageText: "    ",
                timeout: 50,
                allAnimationStop: true,
                emotion: "none",
                nextMessage: true,
            }),
            new Dialog({
                messageText: "      " + Player.name,
                timeout: 70,
                allAnimationStop: true,
                emotion: "none",
                nextMessage: true,
            }),
            new Dialog({
                messageText: "   ",
                timeout: 30,
                allAnimationStop: true,
                emotion: "none",
                callbackOutside: () => {
                    new Howl({
                        src: laughing
                    }).play();
                }
            }),
            new Dialog({
                messageText: "    ,   -    ",
                allAnimationStop: true,
                emotion: "none",
            }),
            new BackgroundDialog({
                callbackOutside: () => {
                    scene.changeMusic(sadHorror)
                    messageInfo.setAnnaChange(true);
                },
                messageText: "?",
                emotion: "none",
                allAnimationStop: true
            }),
            new BackgroundDialog({
                messageText: "    ?!",
                emotion: "none",
                allAnimationStop: true,
                callbackOutside: () => {
                    Scene.background = null;
                }
            }),
            new BackgroundDialog({
                messageText: "    ,  ,   !",
                emotion: "none",
                allAnimationStop: true
            }),
            new BackgroundDialog({
                messageText: Dialog.genderMessage(Player.gender, {
                    maleMessage: "  ?",
                    femaleMessage: "  ?"
                }),
                emotion: "none",
                allAnimationStop: true
            }),
            new BackgroundDialog({
                messageText: "    .  ,   . ,   ,  ,      , ",
                emotion: "none",
                allAnimationStop: true
            }),
            new BackgroundDialog({
                messageText: "      ",
                emotion: "none",
                allAnimationStop: true
            }),
            new BackgroundDialog({
                messageText: ",      ",
                timeout: 80,
                emotion: "none",
                allAnimationStop: true,
            }),
            new BackgroundDialog({
                messageText: "",
                timeout: 120,
                emotion: "none",
                allAnimationStop: true,
            }),
            new BackgroundDialog({
                messageText: " ",
                timeout: 120,
                emotion: "none",
                allAnimationStop: true,
            }),
            new BackgroundDialog({
                messageText: "  ",
                timeout: 120,
                emotion: "none",
                allAnimationStop: true,
            }),
            new BackgroundDialog({
                messageText: "    ",
                timeout: 120,
                emotion: "none",
                allAnimationStop: true
            }),
            new BackgroundDialog({
                messageText: " ",
                timeout: 0,
                emotion: "none",
                allAnimationStop: true,
                callbackOutside: () => {
                    document.querySelector(".novel").remove();
                    window.scrollTo({
                        top: 0,
                        behavior: "smooth"
                    });
                    localStorage.setItem("finished", "true");
                },
            })
        ]
    )
}

function changePlayerVoice(gender, scene){
    switch(gender){
        case "":
            Scene.voice = Scene.voices.maleVoice;
            break;
        case "":
            Scene.voice = Scene.voices.femaleVoice;
            break;
    }
}